<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-transport', data)" class="btn btn-info btn-sm">Lihat Detail</a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData.detail" id="modal-rincian-transport" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="text-center mb-5">
                <h5 class="mb-1 font-weight-bold">Detail {{ config.title }}</h5>
                <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">MPP</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.is_mpp | parse('ynBool') }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div v-if="modalData.detail" class="row">
          <div class="col-md-5">
            <ul class="list-unstyled mb-0">
              <li class="mb-5">
                <s-input v-model="modalData.detail.base_value" :item="{ type:'number', validation:[], label:'Nilai Dasar', prefix: 'Rp. ', groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:2 } }" />
              </li>
              <li class="mb-3">
                <label class="checkbox checkbox-md">
                  <input type="checkbox" v-model="modalData.detail.is_use_presence_data" name="bpjs_perusahaan">
                  <span class="mr-3"></span>Perhitungan Tunjangan menggunakan data Rekap Presensi</label>
              </li>
            </ul>
            <hr>
            <table class="table no-border mt-5">
              <tr>
                <td class="pt-0 pl-0 nowrap-table">Tunjangan</td>
                <td class="pt-0 pl-0 nowrap-table">:</td>
                <th class="pt-0 pr-5">
                  <div class="d-flex justify-content-between">
                    <span class="">Rp.&emsp;</span>
                    <span class="text-right font-weight-bold">{{ modalData.detail.tunjangan_value || 0 | parse('number') }}</span>
                  </div>
                </th>
              </tr>
              <tr>
                <td class="pt-0 pl-0 nowrap-table">Potongan</td>
                <td class="pt-0 pl-0 nowrap-table">:</td>
                <th class="pt-0 pr-5">
                  <div class="d-flex justify-content-between">
                    <span class="">Rp.&emsp;</span>
                    <span class="text-right font-weight-bold">{{ modalData.detail.potongan_value || 0 | parse('number') }}</span>
                  </div>
                </th>
              </tr>
              <tr>
                <td class="pt-0 pl-0 nowrap-table">Penerimaan</td>
                <td class="pt-0 pl-0 nowrap-table">:</td>
                <th class="pt-0 pr-5">
                  <div class="d-flex justify-content-between">
                    <span class="">Rp.&emsp;</span>
                    <span class="text-right font-weight-bold">{{ modalData.detail.penerimaan_value || 0 | parse('number') }}</span>
                  </div>
                </th>
              </tr>
            </table>
            <div class="mt-5">
              <button @click="submitData('modal-rincian-bills')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
            </div>
          </div>
          <div class="col-md-7">
            <div class="py-3 px-5 border-primary mt-2 bg-light-primary" style="border:2px solid;border-radius:8px">
              <h5>Data Rekap Presensi</h5>
              <table v-if="modalData.detail.data_employee_presence_recaps" class="table no-border">
                <tr>
                  <td class="py-0 nowrap-table pl-0">Jumlah Hari Kerja</td>
                  <td class="py-0 nowrap-table pl-0">:</td>
                  <td class="py-0 "> {{ modalData.detail.data_employee_presence_recaps.work_day_count || 0 | parse('number') }} Hari</td>
                </tr>
                <tr>
                  <td class="py-0 nowrap-table pl-0">Tidak Masuk</td>
                  <td class="py-0 nowrap-table pl-0">:</td>
                  <td class="py-0 ">{{ modalData.detail.data_employee_presence_recaps.work_day_lost_count || 0 | parse('number') }} Hari</td>
                </tr>
                <tr>
                  <td class="py-0 nowrap-table pl-0">Terlambat</td>
                  <td class="py-0 nowrap-table pl-0">:</td>
                  <td class="py-0 ">{{ modalData.detail.data_employee_presence_recaps.work_day_late_count || 0 | parse('number') }} Hari, {{ modalData.detail.data_employee_presence_recaps.work_day_minute_late_count || 0 | parse('number') }} Menit</td>
                </tr>
              </table>
              <h5>Apabila menggunakan Rekap Presensi :</h5>
              <ul class="pl-5 font-size-sm">
                <li>Nilai Tunjangan didapat dari : <code>Nilai Dasar x (Jumlah Hari Kerja - Tidak Masuk)</code> </li>
                <li>Nilai Potongan didapat dari :<ul class="pl-5">
                    <li>Jika Jumlah hari terlambat 5 Hari / 60 Menit : <code>Nilai Dasar x 50%</code> </li>
                    <li>Jika Jumlah hari terlambat lebih dari 5 Hari / lebih dari 60 Menit : <code>Nilai Dasar x 100%</code></li>
                  </ul>
                </li>
                <li>Nilai Penerimaan didapat dari : <code>Nilai Tunjangan - Nilai Potongan</code> </li>
              </ul>
            </div>
          </div>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-tunjangan-transport-details',
  data () {
    return {
      modalData: {},
      mainKey: 0,
      config: {
        title: 'Data Tunjangan Transport',
        model_api: null,
        getter: 'custom/tunjangan-transport-details',
        setter: 'tunjangan-transport-details',
        pk_field: 'name',
        filter_api: {},
        custom_api: {
          create: 'custom/tunjangan-transport-details'
        },
        permission: {
          create: 'create-tunjangan-transport-details',
          read: 'view-tunjangan-transport-details',
          update: 'update-tunjangan-transport-details',
          delete: 'delete-tunjangan-transport-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id' },
              create: false,
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id', show: false },
              create: false,
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          },
          {
            id: 'is_use_presence_data',
            label: 'Presensi',
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: false,
              create: false,
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          },
          { id: 'penerimaan_value', label: 'Penerimaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'base_value', label: 'Nilai Dasar', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'tunjangan_value', label: 'Tunjangan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'potongan_value', label: 'Potongan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        this.$refs['sub-module'].$children[1].getData()
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('get', 'custom/tunjangan-transport-details/show', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    copyValue (obj = null) {
      if (!obj) {
        this.$set(this.modalData, 'detail', { ...this.modalData.detail, ...this.modalData.sync.data_recaps })
      }
    },
    submitData () {
      const api = this.config.getter + '/calculate'
      this.$_api.post(api, this.modalData.detail).then(res => {
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
        this.$set(this.modalData, 'detail', res.data)
        this.mainKey++
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



